<script>
  import BracketsMarionette from "../../images/brackets-marionette.png";
  import BracketsLifts from "../../images/brackets-lifts.png";
  import BracketsSharesHeart from "../../images/brackets-shares-heart.png";
  import Step from "./Step.svelte";
</script>

<div class="root">  
  <div class="separator" />
  <h1>Matching</h1>
  <div class="separator" />
  <Step stepNumber="1" name="Strings">
    <p>
      The first thing I did was break up a sentence into parts. I defined a
      string for each part to look for in the sentence. I gave a point for each
      time a string exact matched anywhere in the sentence. The score was the
      number of matches divided by the number of parts.
    </p>

    <img
      class="image"
      src={BracketsMarionette}
      alt="Brackets character using a marionette"
    />
    <p>
      Next, I supported alternatives strings for each part of a sentence. I
      defined a set of strings for each part and gave a point if one of the
      strings in the set matched. In machine learning, this is often called a
      phrase dictionary.
    </p>
  </Step>
  <Step stepNumber="2" name="Words">
    <p>
      I now needed to prevent substring false positives such as matching "the"
      within "there". To do this, I needed to be words rather than characters. I
      created a basic tokenizer that breaks up words by spaces, punctuation, and
      other delimiters.
    </p>
  </Step>
  <Step stepNumber="3" name="Phrases">
    <p>
      I wanted a search better suited for finding words in sentences rather than
      only character matches. I knew about the Trie tree structure which is used
      in full-text search. It is very efficient in finding multiple possible
      phrases within a sentence at once. This was exactly what I needed for
      matching strings in my phrase dictionary.
    </p>
    <p>
      To implement a Trie search, I defined the Trie node data structure. I
      create a character range, a token range, and a token map to translate
      between them. I discovered and fixed several issues with the pseudo-code
      description of the Trie algorithm I found online. I wrapped up by adding
      an ID system to identify which phrase I matched. This let me handle
      matching a shorter phrases within matches of longer phrases.
    </p>
  </Step>
  <Step stepNumber="4" name="Patterns">
    >
    <p>
      I knew that phrase matching would only go so far. I needed to be able to
      match string patterns like dates, URLs, or zip codes. I added regular
      expression matching with some constraints built in to match phrases in a
      sentence.
    </p>
    <p>
      While, I'm pretty good with regular expressions, I dread them. Even a
      simple pattern requires a bit of complicated syntax that can be difficult
      to figure out and maintain. To make pattern matching easier, I added a
      string format to express simple patterns. For example a social security
      number could be ###-##-####.
    </p></Step
  >
  <Step stepNumber="5" name="Never Parts"
    ><p>
      There are words and phrases that negate meaning or would indicate a
      different intention. I added defining never parts using the same ways to
      match as parts. If any one of the never parts matches, it results in a
      zero score.
    </p></Step
  >
  <div class="separator" />
  <h1>Scoring</h1>
  <div class="separator" />
  <Step stepNumber="6" name="Order"
    >
    <p>
      At this point, we can effectively match phrases and patterns in a
      sentence. The initial scoring left a lot to be desired, but it needed
      to remain straightforward and deterministic.
    </p>
    <p>
      Beyond counting matches, the order of matches is an important signal to
      consider. I increased the score for each in-order match. Since many
      sentences are understandable even if they are completely out of order, I
      capped the penalty. I also add a flag to each part definition to ignore
      its order during scoring.
    </p>
  </Step>
  <Step stepNumber="7" name="Weight"
    ><p>
      Some parts of a sentence are more important to match that others. I added
      a weight, with a default of 1, to each part.
    </p>
    <img
      class="image"
      src={BracketsLifts}
      alt="Brackets character lifting weights"
    />
    <p>
      I updated my scoring algorithm to measure each match multiplied by its
      weight. This causes the score to be the sum of the relative weights rather
      than counts.
    </p>
  </Step>
  <Step stepNumber="8" name="Noise">
    <p>
      When there are parts that don't match, it can add confusion to
      understanding the sentence. I decreased the score for each noise token.
      This noise shouldn't signifcantly reduce score, so I capped this penalty
      too.
    </p>
  </Step>
  <Step stepNumber="9" name="Required">
    <p>
      Some parts of a sentence must be there to match the intent of the
      sentence. To handle required parts, I added a flag to each part where if
      that part does not match, the resulting score is zero.
    </p>
  </Step>
  <Step stepNumber="10" name="Optional">
    <p>
      Other parts are nice to have, but completely optional. If a weight is set
      to zero, that parts score is not considered and it becomes optional.
    </p>
  </Step>
  <div class="separator" />
  <h1>Extraction</h1>
  <div class="separator" />
  <Step stepNumber="11" name="Variables"
    ><p>
      Now we have a recognizer that has good ways to match parts of a
      sentence and return a score based on ordering, noise, relative
      weights, and required/optional parts. We can define an set of examples
      and expected parts to find out how well a sentence matches the intent.
    </p>
    <p>
      I added a variable name field to each part. When a part with a variable
      matches, it is included in the recognition result. Because a part may have
      multiple matches that vary by start position and length, each variable
      match can return an array of matching values. The values are sorted from
      best to worst match.
    </p>
  </Step>
  <Step stepNumber="12" name="Ranking"
    ><p>
      I wrote a match sorting algorithm that orders the variable values in the
      context of matching parts across the sentence.
    </p>
    <ol>
      <li>
        new matches: A match from a previous part is sorted after new matches
      </li>
      <li>
        after previous matches: A match that starts after the best previous part
        match is sorted earlier than other matches.
      </li>
      <li>
        earlier matches: A match starting earlier in the sentence is sorted
        earlier that other matches
      </li>
      <li>
        longer: A match with greater length is sorted earlier than other matches
      </li>
    </ol>
  </Step>
  <div class="separator" />
  <h1>Bells and Whistles</h1>
  <div class="separator" />
  <Step stepNumber="13" name="Parse"
    ><p>
      In order to debug, I wanted to be able to understand exactly what the
      recognizer matched. I added text ranges for each match, of each part, of
      each example to the results. I also returned the token map to inspect the
      text ranges by token or character range.
    </p>
  </Step>

  <Step stepNumber="14" name="Metrics"
    ><p>
      It is also helpfulto know how the recognizer scored a sentence. I added
      the set of metrics that are used to calculate the score.
    </p>
  </Step>

  <Step stepNumber="15" name="Sharing"
    ><img
      class="image"
      src={BracketsSharesHeart}
      alt="Brackets characters sharing a heart"
    />
    <p>
      Any application trying to recognize text is probably going to want to
      recognize multiple different intents. Some of these intents may have
      examples that have similar or duplicate phrases or patterns for their
      parts. I added a shared dictionary of phrases and patterns to allow the
      caller to reference them from an intent without having to retype them.
    </p>
  </Step>

  <Step stepNumber="16" name="Customization"
    ><p>
      The recognizer interface is defined to as a general purpose method for
      text recognition. It takes the text to recognize, the intent, and some
      options. It returns a score between 0 and 1 (inclusive) and the extracted
      variable name/values pairs. Another implementation could be substituted
      for this recognizer.
    </p>
    <p>
      The options also allow some limited customization. A different tokenizer
      can be passed for handling different languages. The noise and out-of-order
      penalties can be adjusted.
    </p>
  </Step>

  <div class="separator" />
</div>

