<script>
  import BracketsAccountable from "../../images/brackets-reports-status.png";
  import BracketsDabs from "../../images/brackets-dabs.png";
  import BracketsHat from "../../images/brackets-kid.png";
  import BracketsMeditates from "../../images/brackets-meditates.png";
  import BracketsPlans from "../../images/brackets-plans.png";
  import BracketsStickyNotes from "../../images/brackets-sticky-notes.png";

  import Tip from "./Tip.svelte";
</script>

<div class="article">  
  <Tip
    title="Each morning, choose three things to get done"
    image={{
      src: BracketsStickyNotes,
      alt: "Brackets character holding up sticky notes",
    }}
  >
    <svelte:fragment slot="details">
      <ol>
        <li>The most important, difficult, and urgent task.</li>
        <li>A task that can be achieved in one day or less.</li>
        <li>Something you will enjoy and learn something new.</li>
      </ol>
    </svelte:fragment>
    <svelte:fragment slot="description">
      <p>
        Ignore the remaining ocean of work. Work on the first item first and
        switch to the second item if you get blocked. If you finish the first
        two, reward yourself with the third.
      </p>
      <p>
        Choosing three tasks like this helps you make progress on the most
        challenging work, get at least one thing done each day, and avoid
        burnout.
      </p>
    </svelte:fragment>
  </Tip>
  <Tip
    title="Plan each week in solitude"
    image={{
      src: BracketsPlans,
      alt: "Brackets character planning",
    }}
    layout="summary-image"
  >
    <svelte:fragment slot="details">
      <ul>
        <li>Take 30 minutes on Monday, alone, to frame your week.</li>
        <li>
          Pick a theme for the week. Consider your team's goals for the current
          period.
        </li>
        <li>
          Block out time on your calendar to get work done, learn, and to rest
          between major efforts.
        </li>
      </ul>
    </svelte:fragment>
    <svelte:fragment slot="description">
      <p>
        Think of your available time like money you're going to invest. Where
        should you allocate your time to get the highest return? You will get
        more done if you decide for yourself at the start of the week.
      </p>
      <p>
        Grouping planned work into a theme and blocking out time helps avoid
        context switching and meetings from taking over your calendar.
      </p>
    </svelte:fragment>
  </Tip>
  <Tip
    title="Practice flow"
    image={{
      src: BracketsMeditates,
      alt: "Brackets character meditating",
    }}
  >
    <svelte:fragment slot="details">
      <ul>
        <li>
          Try techniques like pomodoro. Set 20 minute timers to focus on one
          things at a time.
        </li>
        <li>Create a ritual cadence for your day.</li>
        <li>Be unavailable for distraction when you are concentrating.</li>
        <li>
          Avoid checking email or text messages first thing. Instead jump into
          flow first.
        </li>
      </ul>
    </svelte:fragment>
    <svelte:fragment slot="description">
      <p>
        Flow is when you are entirely focused on your current work. You tune out
        distraction, stop watching the clock, and make meaningful progress.
      </p>
      <p>
        Practicing concentration techniques and having a consistent cadence for
        your day trains your brain to quickly get into deep flow.
      </p>
      <p>
        Train others to not interrupt you when you are thinking. Set a busy
        status on your computer, turn off notifications, and consider a sign or
        light that indicates you are not available.
      </p>
    </svelte:fragment>
  </Tip>
  <Tip
    title="Communicate with accountability"
    image={{
      src: BracketsAccountable,
      alt: "Brackets character reporting status",
    }}
    layout="summary-image"
  >
    <svelte:fragment slot="details">
      <ul>
        <li>Commit to completing specific, atomic tasks each day.</li>
        <li>
          Prefer action verbs for tasks like 'create', 'update', or 'fix'.
        </li>
        <li>Be prepared to give status to your team at any time.</li>
        <li>
          Call yourself out when you don't get something done or make a mistake.
        </li>
        <li>
          Avoid excuses for failures. State how you will do better in the
          future.
        </li>
        <li>Don't be afraid to ask for help when blocked.</li>
      </ul>
    </svelte:fragment>
    <svelte:fragment slot="description">
      <p>
        Setting an example of personal accountability will make you and your
        team more productive. When your teammates know you are open and honest
        about your work, they will trust you and want to help you succeed.
      </p>
      <p>
        When a task atomic (either complete or incomplete) and action oriented,
        you avoid thrashing on a task day after day with the same 'I'm working
        on it' status report.
      </p>
    </svelte:fragment>
  </Tip>
  <Tip
    title="Listen to your inner kid"
    image={{
      src: BracketsHat,
      alt: "Brackets character wearing a hat",
    }}
    layout="summary-image"
  >
    <svelte:fragment slot="details">
      <ul>
        <li>Eat when hungry</li>
        <li>Nap when tired</li>
        <li>Exercise regularly</li>
        <li>Relax when stressed</li>
        <li>Step away and amble when blocked</li>
      </ul>
    </svelte:fragment>
    <svelte:fragment slot="description">
      <p>
        Kids run and play in intervals. You should too. Burnout doesn't happen
        in a day and it can't be fixed in a day. Managing your energy levels
        will make you more productive and avoid making mistakes.
      </p>
      <p>
        When you are blocked on a difficult problem, take a slow walk for 30
        minutes. Your brain will make an insightful leap when you give it some
        time to background process.
      </p>
    </svelte:fragment>
  </Tip>
  <Tip
    title="Record your accomplishments"
    image={{
      src: BracketsDabs,
      alt: "Brackets character dabbing",
    }}
  >
    <svelte:fragment slot="details">
      <ul>
        <li>Write down what you got done each week.</li>
        <li>Consider sending your accomplishments to your team and/or boss.</li>
        <li>Reflect to wrap up and enjoy the weekend.</li>
      </ul>
    </svelte:fragment>
    <svelte:fragment slot="description">
      <p>
        Having a list of all the great work you got done will be immensely
        valuable to writing up your review.
      </p>
      <p>
        Depending on your team, boss, and status meetings you can decide if you
        want to communicate your accomplishments each week or month.
      </p>
      <p>
        Taking 5 minutes to reflect on what you got done can help you step off
        the infinite work treadmill, leave work at a work, and really enjoy your
        weekends.
      </p>
    </svelte:fragment>
  </Tip>
</div>

