<script lang="ts">
  import type { StepCalloutAlignment } from "./types";

  export let step: number;
  export let name: string;
  export let align: StepCalloutAlignment;

  $: stepClassName =
    align === "toggle"
      ? step % 2 === 0
        ? "step right"
        : "step left"
      : `step ${align}`;
</script>

<div class={stepClassName}>
  <div class="number">{step}</div>
  <div class="line" />
  <div class="name">{name}</div>
</div>

