<script lang="ts">
  import type { StepCalloutAlignment } from "./types";
  import StepCallout from "./StepCallout.svelte";

  export let stepNumber: number;
  export let name: string;

  $: stepClassName = stepNumber % 2 === 0 ? "step even" : "step odd";

  let clientWidth;
  $: calloutAlignment = (
    clientWidth > 900 ? "toggle" : "left"
  ) as StepCalloutAlignment;
</script>

<div class={stepClassName} bind:clientWidth>
  <div class="callout">
    <StepCallout step={stepNumber} {name} align={calloutAlignment} />
  </div>
  <div class="prose">
    <slot />
  </div>
</div>

<style>
  .step {
    align-items: flex-start;
    column-gap: 2em;
    display: grid;
    grid-template-columns: 250px 350px 250px;

    justify-content: center;
  }

  .odd {
    grid-template-areas: "callout prose .";
  }

  .even {
    grid-template-areas: ". prose callout";
  }

  .callout {
    grid-area: callout;
    width: 100%;
  }

  .prose {
    font-family: "Merriweather", serif;
    max-width: 350px;
    grid-area: prose;
    align-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
  }

  @media only screen and (max-width: 900px) {
    .step {
      align-items: center;
      column-gap: 2em;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
  }
</style>
