<div class="root centered-column ">
  <div class="section-header">Avoid commenting the what</div>
  <div class="two-columns">
    <div class="prose">
      <p>
        Comments that explain what the code is doing are only valuable to those
        unfamiliar with the language. The code itself describes exactly what
        will happen when it runs. This precision is the entire point of
        programming lanugages (vs. natural languages)
      </p>
      <p>
        Adding <i>what</i> comments may be a habit created when learning to program.
        Students often pseudo-code the sequence of statements in comments. It is
        easy to forget to clean them up after the code is written.
      </p>
    </div>
    <div class="example">
      <div class="header">Low value <i>what</i> comments</div>
      <div class="code">
        <pre class="comment">
// Declare constants
      </pre>
        <pre>
public const double CompressionRatio = 0.75;
public const string JpegImageType = "JPEG";
  
public void Save()
&#123
</pre>
        <pre class="comment">
  // Save the image
</pre>
        <pre>
  this._image.Save(this._data, CompressionRatio, JpegImageType);
&#125    
</pre>
      </div>
    </div>
  </div>
  <div class="separator" />
  <div class="section-header">Always comment the why</div>
  <div class="two-columns">
    <div class="prose">
      <p>
        The primary question everyone has when reading new code is <i>why</i> it
        was written this way.
      </p>
      <ul>
        <li>
          Why does the code recalculate the database index before and after a
          new row was written?
        </li>
        <li>
          Why did this call this function instead of that similar function?
        </li>
        <li>
          Why is this object serialized with a boolean flag at the beginning?
        </li>
        <li>
          Why does this async function lock the entire object rather than only
          the field it changes?
        </li>
      </ul>
      <p>
        If you care about the longevity of your code and want your hard work
        appreciated, comment why.
      </p>
      <p>
        Describe your reasons for decisions. Call out the nuances of how you
        crafted the code.
      </p>
    </div>
    <div class="example">
      <div class="header">A high value <i>why</i> comment</div>
      <div class="code">
        <pre
          class="comment">                              
// Compress ratio 0.75 minimzes disk space with no noticable image quality loss
      </pre>
        <pre> 
public const double CompressionRatio = 0.75;

public const string JpegImageType = "JPEG";
  
public void Save()
&#123;
  this._image.Save(this._data, CompressionRatio, JpegImageType);
&#125;
</pre>
      </div>
    </div>
  </div>
  <div class="separator" />
  <div class="section-header">Prefer concise single sentences</div>
  <div class="two-columns">
    <div class="prose">
      <p>
        Concise yet informative comments makes your code easier to understand.
        Flowery platitudes, word play, and thesaurus lookups are best left for
        poetry.
      </p>
      <ul>
        <li>Prefer each comment is a single sentence on a single line.</li>
        <li>Avoid compound sentences (i.e. sentences combined with and/or)</li>
        <li>
          Avoid conditional clauses (i.e. sentences with ifs, whens, and buts)
        </li>
        <li>
          Avoid unnecessary adjectives and adverbs (e.g. easy, fast, powerful,
          quickly)
        </li>
      </ul>
    </div>
    <div class="example">
      <div class="header">Some wordy comments</div>
      <pre
        class="code comment">                    
/* 
The database record is updated by including a simple client ID and it is combined with a generated seed value.
The row id exists to ensure that rows generated by each client can be retrieved easily later on.  
The row ID is a GUID that is base-64 encoded as a string and is then reversed for maximum cluster distribution. 
*/      
          </pre>
      <div class="header">More concise comments</div>
      <pre
        class="code comment">                    
/*         
Created a unique row ID by combining the client ID with the generated seed.
The row ID is reversed then base-64 encoded for cluster distribution.               
*/
          </pre>
    </div>
  </div>
  <div class="separator" />
  <div class="section-header">Prefer active voice</div>
  <div class="two-columns">
    <div class="prose">
      <p>
        Running code takes action. The voice of comments should reflect
        activity. Use verbs associated with the action or decision you made in
        the code.
      </p>
      <p>
        Passive voice has repetative extra words and is harder to read. You may
        need passive voice when writing your next novel, but less often in
        comments.
      </p>
    </div>
    <div class="example">
      <div class="header">A passive voice comment</div>
      <div class="code">
        <pre
          class="comment">
    // To avoid deadlocks the customer and product lists are locked.
        </pre>
      </div>
      <div class="header">An active voice comment</div>
      <div class="code">
        <pre
          class="comment">                  
    // Locked customer and product lists to avoid deadlocks.
        </pre>
      </div>
      <div class="header">An alternative active voice comment</div>
      <div class="code">
        <pre
          class="comment">                
    // Avoided deadlocks by locking both customer and product lists.
        </pre>
      </div>
    </div>
  </div>
  <div class="separator" />
  <div class="section-header">Avoid comments for the sake of comments</div>
  <div class="two-columns">
    <div class="prose">
      <p>
        Keep in mind comments are for humans, not for computers. You want to get
        the highest ink-to-value ratio possible. Comments should only be added
        where they provide value to the reader.
      </p>
      <p>
        Don't use tools that auto-generate comments in your source. If you <i
          >must</i
        >
        publish online documentation and every single thing <i>must</i>
        be documented to meet a conformance requirement, see if your build system
        can generated missing comments at build time.
      </p>
    </div>
    <div class="example">
      <div class="header">A auto-generated comment with little value</div>
      <div class="code">
        <pre class="comment">
// Gets and sets the Name
        </pre>
        <pre>
public string Name &#123; get&semi; set&semi; &#125;   
        </pre>
      </div>
    </div>
  </div>
</div>

