<div class="root centered-column ">
  <div class="section-header">Tools organize code for computers</div>
  <div class="three-columns">
    <div class="prose">
      <p>
        The conventional coding standard for many projects requires grouping
        code by access (e.g. public, protected, private), by member type (e.g.
        properties, methods), or by lifetime (e.g. constants, instance types,
        static types).
      </p>
    </div>
    <div class="prose">
      <p>
        These rules were defined based on programming languages where the order
        of statements in the text file determined the compiled memory layout.
        Following the same rules in modern languages and when modern editors is
        an archaic process.
      </p>
    </div>
    <div class="prose">
      <p>
        Avoid grouping your code based on access, lifetime, or any way prefered
        by the computer. This is why we have text programming languages rather
        than typing ones and zeros.
      </p>
    </div>
  </div>
  <div class="separator" />
  <div class="section-header">Locality helps organize code for humans</div>
  <div class="three-columns">
    <div class="prose">
      <p>
        The principle of locality says that things nearby in time or space are
        likely to be referenced in the near future.
      </p>
      <p>
        Code should be organized to optimize readability and maintainability.
        Applying the principle of locality groups things nearby that are likely
        to be referenced together or change together.
      </p>
    </div>
    <div class="prose">
      <p>
        Here are the definitions from <a
          href="http://en.wikipedia.org/wiki/Locality_of_reference">Wikipedia</a
        >
      </p>
      <ul>
        <li>
          If a particular location is referenced at a particular time, then it
          is likely that nearby locations will be referenced in the near future
          (Spatial Locality).
        </li>
        <li>
          If at one point in time a particular location is referenced, then it
          is likely that the same location will be referenced again in the near
          future (Temporal Locality).
        </li>
      </ul>
    </div>
    <div class="prose">
      <p>
        Locality has been an important principle in computer storage, video
        optimization, and caching.
      </p>
      <p>
        You can use the same principle to better organize your code for human
        productivity.
      </p>
    </div>
  </div>
  <div class="separator" />
  <div class="section-header">The principle in practice</div>
  <div class="three-columns">
    <div class="prose">
      <p>
        Keep fields near the functions that use them. Keep subroutines near the
        functions that call them. If one function is referenced, then it is
        likely that the fields and functions it uses will be referenced in the
        near future.
      </p>

      <p>
        Sometimes you can’t group everything in perfect clusters. Fall back to
        grouping things that are most likely to change together and at the same
        time.
      </p>
    </div>
    <div class="prose">
      <p>
        If you have trouble organizing a particular module or class, check if it
        has very independent groups of fields or methods. It may be that you
        need to break up your code into smaller pieces where each has a single
        responsibility.
      </p>
      <p>
        The great thing is that by using locality, you’ve already grouped
        related things together - making them easy to put in a new file.
      </p>
    </div>
    <div class="prose">
      <p>
        Leverage the modern IDE at your finger tips. Search, reference
        following, and direct lookup capabilities of IDEs can quickly navigate
        to the code you are looking for.
      </p>
      <p>
        Don’t worry too much that you don’t have all the data fields grouped
        together; a modern IDE can usually give you that view without causing
        you to re-organize your code.
      </p>
    </div>
  </div>
  <div class="separator" />
  <div class="section-header">Benefits</div>
  <div class="two-columns">
    <div class="list">
      <ul>
        <li>Reduces scrolling when seeking for related fields and methods</li>
        <li>
          Helps other understand the code faster as cohesive related units
        </li>
        <li>Avoids missing a necessary cascading change to related code</li>
      </ul>
    </div>
    <div class="list">
      <ul>
        <li>Reduces scatter of changes in code reviews and pull requests</li>
        <li>
          Don’t have to run tools to enforce annoying rules because you have
          semantically organized the code.
        </li>
      </ul>
    </div>
  </div>
</div>

