<script lang="ts">
  import FrontPage from "./frontPage/FrontPage.svelte";
  import Modal from "./Modal.svelte";
  import { modal } from "./stores";
  
  let modalOpen = false;
  let previousModal;

  $: {    
    if ($modal != undefined && !previousModal) {
      modalOpen = true;
    }    

    previousModal = modalOpen ? $modal : undefined;
  }  

  $: rootClass = modalOpen ? "root no-scroll" : "root";
  $: appClass = modalOpen ? "app blur" : "app";
</script>

<!--
  @component
  The demo application
-->
<div class={rootClass}>
  <div class={appClass}>
    <div class="header"></div>
    <div class="content">
      <FrontPage />
    </div>
    <div class="footer" />
  </div>
  {#if modalOpen}
    <Modal bind:open={modalOpen} title={$modal.title} subtitle={$modal.subtitle}><svelte:component this={$modal.component} /></Modal>
  {/if}
</div>

<style>
  .root {
    height: 100%;
    overflow: auto;
    padding: 0;
    margin: 0;
    width: 100%;
  }

  .app {
    display: grid;
    grid-template-rows: auto 1fr auto;
    grid-template-columns: 1fr;
    grid-template-areas: "header" "content" "footer";
