      </p>
      <p>Paying attention to naming is critical to software architecture.</p>
      <p>
        Good names make developers more productive. Well named code is easier to
        understand, easier to maintain, less likely to have bugs, and requires
        fewer code comments.
      </p>
    </div>
    <div class="prose">
      <p>
        Finding the perfect name isn’t easy. It is a problem made messy by
        natural language.
      </p>

      <ul>
        <li>There are often multiple definitions for the same word.</li>
        <li>
          People can interpret words differently and have personal preferences.
        </li>
        <li>
          We build on top of a history of existing names and an evolving
          dictionary.
        </li>
      </ul>
    </div>
  </div>
  <div class="separator" />
  <div class="section-header">Always look for better names</div>
  <div class="two-columns">
    <div class="list">
      <ul>
        <li>
          Don’t try to pick a perfect name the first time. As you solve
          problems, use what you learn about the problem space.
        </li>
        <li>Write sample caller code and unit tests to evaluate your names.</li>
        <li>
          Let developers unfamiliar with your code read it and see if they can
          understand it without additional explanation.
        </li>
      </ul>
    </div>
    <div class="list">
      <ul>
        <li>
          Comment imperfect names in code. List the names you rejected and why.
        </li>
        <li>
          Keep the cost of renaming low by using the refactoring tools available
          in modern IDEs and version control.
        </li>
      </ul>
    </div>
  </div>
  <div class="separator" />
  <div class="section-header">Prefer correct grammar</div>
  <div class="section-prose">
    <p>
      These are some rules for English. If you code in another language, they
      may not apply.
    </p>
  </div>
  <div class="two-columns">
    <div class="list">
      <ul>
        <li>Adjectives come before nouns.</li>
        <li>Use nouns for structures and classes.</li>
        <li>Use verbs for methods.</li>
        <li>
          Use proper verb tense for events to distinguish between the present
          (e.g. onDrag) and past (e.g. onDropped).
        </li>
      </ul>
    </div>
    <div class="list">
      <ul>
        <li>
          Use singular and plural to indicate if there is one or many (i.e.
          cardinality).
        </li>
        <li>
          Avoid “verbing” nouns unless you are naming a functor. See anthimeria.
        </li>
      </ul>
    </div>
  </div>  
  <div class="separator" />
  <div class="section-header">Avoid distinguishing without a difference</div> 
  <div class="two-columns">
    <div class="list">
      <ul>
        <li>Use the minimal number of words to describe the purpose of the code.</li>
        <li>Add words when you must distinguish one thing from another.</li>        
      </ul>
    </div>
    <div class="list">
      <ul>
        <li>Keep names short by leveraging structural hierarchy (e.g. namespaces, modules).</li>
        <li>
          Avoid adding empty words evident from the code structure (e.g. Object, Type, Struct, Class, Method)
        </li>
      </ul>
    </div>
  </div>  
  <div class="separator" />
  <div class="section-header">Prefer to be humble</div> 
  <div class="two-columns">
    <div class="list">
      <ul>
        <li>Prefer the most concrete name related to your problem domain. While abstraction and encapsulation are important, over-abstract names create disconnect the code from the problem it is trying to solve.</li>
        <li>When faced with several good names, choose the most commonly used name.</li>        
      </ul>
    </div>
    <div class="list">
      <ul>
        <li>Don’t use synonyms for different things and pretend there is some subtle difference only you can see.</li>
        <li>
          Don’t be too clever. No one wants to read code requiring a dictionary and thesaurus.
        </li>
        <li>Don’t use the same names as ones likely to conflict with global scope, popular frameworks, or code keywords.</li>
      </ul>
    </div>
  </div>  
  <div class="separator" />
  <div class="section-header">Avoid false metrics</div> 
  <div class="two-columns">
    <div class="list">
      <ul>
        <li>Don’t overvalue short names. Auto-complete and intelli-sense are everywhere.</li>
        <li>Don’t allow a desire for consistency make you stick with a bad name.</li>        
      </ul>
    </div>
    <div class="list">
      <ul>
        <li>Don’t choose names so they are alphabetized on disk. Modern IDEs can search and browse code easily.</li>
        <li>
          Choose a name for today, not tomorrow. Avoid choosing a “future-proof” name for code you haven’t written.
        </li>        
      </ul>
    </div>
  </div>  
</div>

